  animation: open 0.5s ease-in-out;
  animation-fill-mode: forwards; }

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
  margin-left: 12.5px;
  animation: close 0.5s ease-in-out;
  animation-fill-mode: forwards; }

.gallery-close span {
  position: absolute;
  width: 25px;
  height: 2px;
  top: 50%;
  left: 50%;
  background: #1A2330;
  border-radius: 2px;
  overflow: hidden;
  transition: all 0.3s linear; }

.gallery-close span::before {
  content: "";
  position: absolute;
  width: 0;
  height: 100%;
  top: 0;
  right: 0;
  background: gray;
  transition: all 0.3s linear; }

.gallery-close span:nth-child(1) {
  animation: span-first-off 0.5s ease-in-out;
  animation-fill-mode: forwards; }

.gallery-close span:nth-child(2) {
  animation: span-second-off 0.5s ease-in-out;
  animation-fill-mode: forwards; }

.gallery-close span:nth-child(3) {
  animation: span-third-off 0.5s ease-in-out;
  animation-fill-mode: forwards; }

.gallery-close.on:hover span::before {
  width: 100%;
  transition: all 0.3s linear; }

.gallery-close.on span:nth-child(1) {
  animation: span-first-on 0.5s ease-in-out;
  animation-fill-mode: forwards; }

.gallery-close.on span:nth-child(2) {
  animation: span-second-on 0.5s ease-in-out;
  animation-fill-mode: forwards; }

.gallery-close.on span:nth-child(3) {
  animation: span-third-on 0.5s ease-in-out;
  animation-fill-mode: forwards; }

@keyframes span-first-on {
  0% {
    transform: translate(-50%, -300%); }
  30% {
    transform: translate(-50%, -50%); }
  100% {
    transform: translate(-50%, -50%) rotate(-45deg); } }

@keyframes span-first-off {
  0% {
    transform: translate(-50%, -50%) rotate(-45deg); }
  30% {
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    transform: translate(-50%, -300%); } }

@keyframes span-second-on {
